* {
    box-sizing: border-box;
}
.footer {
    background-color: black;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: blue;
    width: 90%;
    margin: 0px auto 0px;
    position: relative;
}

.footerDiv:nth-child(1){
    color: grey;
    margin-top: 18px;
}
.footerDiv:nth-child(2){
    margin-top: 14px;
    margin-bottom: 20px;
    color: skyblue;
    width: 114px;
    display: flex;
    justify-content: space-between;
}